.start-page-container {
  height: 100%;
  width: 100%;
  background-color: #000;
  display: grid;
  grid-template-rows: calc(100% * 8 / 9 - 0.5px) 1fr;
  grid-row-gap: 1px;
}

.start-page-photos {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 1px;
  min-height: 0;
  min-width: 0;
  cursor: pointer;
}

.start-page-photos li img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

.start-page-photos li {
  max-height: 100%;
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  background-color: #fff;
  min-height: 0;
  min-width: 0;
}

.navigation {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.start-page-photos,
.navigation {
  margin: 0;
  padding: 0;
}

.navigation {
  height: 100%;
  background-color: #fff;
}

.navigation li {
  align-self: center;
  list-style: none;
  padding-left: 21px;
}

.navigation li span {
  text-transform: uppercase;
  cursor: pointer;
}

.navigation li:nth-child(1) {
  grid-column-end: span 1;
}

.navigation li:nth-child(2) {
  grid-column: 3 / 4;
}

.navigation a {
  font-size: 1rem;
  color: #000;
  text-decoration: none;
  text-transform: uppercase;
  align-self: center;
  display: block;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}
